.carousel {
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: relative;
}

.carousel__back-button,
.carousel__next-button {
    position: absolute;
    transform: translateY(-50%);
    top: 50%;
    left: -30px;
    width: auto;
    background-color: transparent;
    border: 1px solid var(--text-color);
    margin-top: -22px;
    color: var(--text-color);
    font-weight: bold;
    font-size: 18px;
    transition: 0.6s ease;
    border-radius: 50%;
    user-select: none;
}

.carousel__next-button {
    left: 100%;
}

.carousel__back-button:hover,
.carousel__next-button:hover {
    background-color: rgba(0,0,0,0.8);
    color: white;
}

.carousel__back-button:disabled,
.carousel__next-button:disabled {
    display: none;
}

.carousel__dot-group {
    margin: 20px 0;
    text-align: center;
}

.carousel__dot {
    border: none;
    background: none;
    margin: 0;
    width: 0;
}

.carousel__dot span {
    height: 5px;
    width: 5px;
    background-color: #bbb;
    border-radius: 50%;
    display: inline-block;
    transition: background-color 0.6s ease;
}

.carousel__dot--selected span,
.carousel__dot span:hover {
    background-color: #0095f6;
}

.carousel__inner-slide {
    display: flex;
    justify-content: center;
    outline: none;
    user-select: none;
}

.carousel img,
.carousel video {
    display: block;
    margin: auto;
    max-width: 100%;
    max-height: 100%;
    border: 1px solid white;
}
